import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { PaginaAulaoComponent } from './pagina-aulao.component';

const routes: Routes = [
  { path: '', component: PaginaAulaoComponent },
];

@NgModule({
  imports: [
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class PaginaAulaoRoutingModule { }
